@import url('https://fonts.googleapis.com/css2?family=Gabarito&display=swap');
:root {
  --main-orange: #FF8303;
  --sub1-orange: #A35709;
  --main-blue: #016A70;
  --sub1-green: #A2C579;
  --sub2-green: #D2DE32;
  --main-grey: #D9D9D9;
  --sub1-grey: #F0E3CA;
  --slate: #748cab;
  --sub1-slate: #bac9dd3d;
  --cream: #f8f6f1;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 1rem;
}
body {
  font-family: 'Gabarito', sans-serif;
  background-color: var(--cream);
  max-width: 100vw;
}
html {
  scroll-behavior: smooth;
}
.logo {
  font-weight: 500;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
nav {
  display: flex;
  align-items: center;
  
}
.nav__list {
  display: flex;
  column-gap: 40px;
}
header {
  position: sticky;
  width: 100%;
  z-index: 999;
  top: 0;
  background-color: var(--cream);
}
header .menu {
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  visibility: hidden;
  opacity: 0;
}
.header__content {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 50px;
  margin: 0 auto;
}

.nav__link {
  color: var(--sub1-orange);
  font-weight: 500;
  transition: 0.2s all;
}
.nav__link:hover {
  color: var(--main-blue);
  font-size: 1.1rem;
}

.logo {
  font-weight: 800;
  stroke: 2px;
  transition: 0.2s all;
}
.logo:hover {
  color: var(--sub1-green);
}

section {
  height: 100vh;
}
.container {
  display: flex;
  padding: 5%;
  align-items: flex-start;
  justify-content: center;
  height: 100%;
}
.home {
  position: relative;
  z-index: 1;
  background: url('./assets/main.jpeg');
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}
.col {
  width: 50%;
}
.action p {
  text-align: center;
}
.left div {
  padding: 1rem 1rem 1rem 0;
  display: flex;
  flex-direction: column;
  row-gap: 1.5rem;
  align-items: center;
  justify-content: center;
  
}
.left h3 {
  font-size: 150%;
  text-align: center;
}
.right img {
  width: 100%;
}

.button {
  padding: 0.5rem;
  font-weight: 700;
  font-size: 1.15rem;
  border: none;
  background-color: var(--main-blue);
  color: var(--cream);
  cursor: pointer;
  border-radius: 10px;
  transition: 0.2s all;
}
.button:hover {
  border-radius: 0;
}

.search {
  overflow:hidden;
  padding: 5%;
  height: auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.search-container {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}

#right-col h1 {
  font-size: 3rem;
  color: var(--main-orange);
}
#right-col p {
  padding: 1% 10%;
}
#left-col {
  display: flex;
  align-items: center;
 
}

.search-bar {
  resize: none;
  height: 5vh;
  width: 100%;
  border: 1px solid rgb(0, 0, 0, 0.5);
  border-radius: 50px;
  text-align: left;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  overflow: scroll;
  display: block;
  
}
.ingredient-submit {
  margin-left: 0.5rem;
  padding: 0.5rem 0.75rem 0.5rem;
  border-radius: 50px;
  border: 1px solid;
  height: 5vh;
  text-align: center;
  background-color: black;
  color: #fff;
  cursor: pointer;
  font-weight: 700;
  transition: 0.2s all;
}
.ingredient-submit:hover {
  color: black;
  background-color: #fff;
}
.about {
  padding: 1.5rem 0 0.5 1rem;
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
}
.actions h1 {
  font-size: 2rem;
  padding: 1rem 0;
}
.actions h1 span {
  font-size: inherit;
  color: var(--main-orange);
}
.about h3 {
  font-size: 1.25rem;
}
.actions {
  padding: 5%;
  width: 50%;
}
.action-container li {
  padding: 0.5rem 0;
}
.action-container p {
  padding: 0.5rem 0;
  font-size: 1.1rem;
}
.action-container span {
  font-weight: 600;
  color: var(--sub1-orange);
  font-size: 1.1rem;
}
.about img {
  width: 50vw;
  height: 100vh;
  object-fit: cover;
  padding: 5%;
}
.action-container a {
  color: var(--slate);
  text-decoration: underline;
  font-weight: 700;
}
.contact-container {
  display: flex;
  padding: 10%;
  height: 100%;
  justify-content: space-between;
}
.contact-left {
  width: 50%;
  display: grid;
}
.icon-links div{
  font-size: 1.25rem;
  
  width: 70%;
}
.contact-left ul {
  padding: 0.5rem 0;
  display: flex;
  justify-content: left;
  width: 70%;
  column-gap: 0.5rem;
}
.contact-left h3{
  font-size: 1.1rem;
  font-weight: 400;
  padding: 1.5rem 0.2rem;
  color: #565155;
}
.contact-left h1 {
  font-size: 3rem;
}
.contact-left p {
  padding: 1rem 0;
  color: #565155;
  width: 70%;
}

.contact-right {
  width: 50%;
  display: block;
  background-color: #1c1a1f;
  border-radius: 10px;
  height: 100%;
}
.contact-right h2 {
  color: #fff;
  padding: 3rem 2rem 0 2rem;
  font-size: 2rem;
}
.input-wrap.w-100 {
  grid-column: span 2;
}
.input-wrap {
  display: grid;
  row-gap: 0.5rem;
}
form {
  padding: 2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2rem;
  row-gap: 1rem;
}
.input-wrap label {
  color: #bfbfbf;
  row-gap: 50px;
}
.contact-input {
  
  background-color: #2c282e;
  border: none;
  width: 100%;
  border-radius: 5px;
  padding: 0.5rem;
}
form div textarea {
  background-color: #2c282e;
  width: 100%;
  padding: 0.5rem;
  color: black;
  resize: none;
  
}

.input-wrap button {
  padding: 1rem;
  border: none;
  background-color: #fff75c;
  color: #1c1a1f;
  font-weight: 600;
  cursor: pointer;
  border-radius: 10px;
}
.food-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 2rem;
  row-gap: 2rem;
  height: 100%;
  overflow:auto;
  width: 100%;
}

.food-result {
  padding: 1rem;
  text-align: center;
  display: grid;
  width: 100%;
  height:auto;
  background-color: var(--sub1-slate);
  row-gap: 10px;
}
.food-result p {
  font-size: 0.9rem;
}
.food-result h2 {
  font-size: 1.25rem;
}
.food-result img {
  width: 100%;
}
.food-result button {
  padding: 0.25rem;
  border-radius: 10px;
  border: 1px solid transparent;
  background-color: var(--main-orange);
  color: black;
  font-weight: 600;
  cursor: pointer;
  transition: 0.2s all;
  width: 100%;
}

.food-result button:hover {
  border-radius: 0;
  font-weight: 900;
}
.food-result p span {
  font-weight: 700;
}
.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}.flex div {
  padding: 0.25rem;
}
.left {
  text-align: left;
}
.right {
  text-align: right;
}
.right h3 {
  font-weight: 400;
}
.right span {
  font-weight: 800;
}
@media all and (max-width: 541px) {
  .contact-container {
    flex-direction: column;
  }
  .contact-left, .contact-right {
    width: 100%;
  }
  .action p {
    background-color: white;
    color: black;
    padding: 5%;
    -webkit-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.75);
box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.75);
  }
  
}
@media all and (max-width: 414px){
  .contact p {
    text-align: center;
  }
  section {
    height: auto;
  }
  .home {
    background-position: center;
  }
  .col {
    width: 90%;
  }
  .container {
    padding: 10%;
  }
  .food-container {
    
    grid-template-columns: repeat(2, 1fr);
  }
  
  .about {
    flex-direction: column;
    padding: 5%;
  }
  .about img {
    width: 100%;
    
  }
  .actions {
    width: 90%;
    text-align: center;
    padding: 1%;
  }
  .contact-container {
    flex-direction: column;
    padding: 10%;
  }
  .contact-left, .contact-right {
    width: 100%;
  }
  .contact-left div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .disclaim p {
    text-align: center;
    width: 100%;
  }
  .icon-links ul {
    align-items: center;
    justify-content: center;
  }
  .icons {
    height: 100px;
  }
}


.footer p{
  padding: 0.25rem;
  text-align: center;
  font-weight: 400;
}

.hamburger {
  display: none;
  height: fit-content;
  cursor: pointer;
  padding: 3px 8px;
  border-radius: 5px;
  transition: 0.2s;
  z-index: 9999;
}
.hamburger:hover {
  background: #fff;
}
.hamburger div {
  width: 30px;
  height: 2px;
  margin: 6px 0;
  background: black;
}
@media all and (max-width: 587px) {
  .food-container {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media all and (max-width: 395px) {
  .food-container {
    grid-template-columns: 1fr;
  }
}
@media all and (max-width: 646px) {
  nav {
      position: absolute;
      right: -300px;
      top: 0;
      width: 280px;
      height: 100vh;
      background-color: var(--cream);
      transition: 0.2s;
      flex-direction: column;
      padding: 5%;
      justify-content: center;
  }
  .show-menu {
      right: 0;
  }
  nav ul {
      flex-direction: column;
      padding: 0 20px;
      margin-top: 30px;
      justify-content: center;
      height: 100%;
      gap: 10%;
  }
  nav ul li a {
      margin-bottom: 5px;
      padding: 10px 15px;
      border-radius: 5px;
  }
  .hamburger {
      display: block;
  }
}